@import "normalize.css";
@font-face {
  font-weight: normal;
  font-family: "Open Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-weight: bold;
  font-family: "Open Sans";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 15px;
  line-height: 20px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
  font-style: normal;
  background-color: #ffffff;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button-link {
  margin: 0;
  display: inline-block;
  vertical-align: top;
  box-sizing: border-box;
  border: 2px solid #444444;
  font-size: 17px;
  line-height: 20px;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  color: #444444;
  padding: 14px 0;
  text-align: center;
  background-color: #ffffff;
  cursor: pointer;
  width: 100%;
}
.page-main {
  margin-top: 1px;
  margin-bottom: 40px;
}
.page-main__header {
  background-color: #f1f1f1;
  padding: 70px 0 76px;
  margin-bottom: 30px;
}
.page-main__wrapper {
  width: 260px;
  margin: 0 calc(50% - (260px / 2));
}
.page-main__title {
  margin: 0;
  font-size: 27px;
  line-height: 30px;
  font-weight: bold;
  text-align: center;
}
@media (min-width: 768px) {
  .page-main {
    margin-bottom: 57px;
  }
  .page-main__form {
    margin-bottom: 50px;
  }
  .page-main__header {
    position: relative;
    padding: 97px 0 80px;
    margin: -43px 0 80px;
  }
  .page-main__header::after {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: -12px;
    height: 24px;
    background-image: url("../img/bg-zigzag-fill.svg");
    background-repeat: repeat-x;
    background-position-x: 0;
    background-size: 768px 24px;
  }
  .page-main__wrapper {
    width: 670px;
    margin: 0 calc(50% - (670px / 2));
  }
  .page-main__title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .page-main {
    margin-bottom: 105px;
  }
  .page-main__form {
    margin-bottom: 50px;
  }
  .page-main__header {
    position: relative;
    padding: 94px 0 105px;
    margin: -42px 0 127px;
  }
  .page-main__header::after {
    bottom: -17px;
    height: 34px;
    background-size: 1150px 34px;
  }
  .page-main__title {
    font-size: 53px;
    line-height: 57px;
  }
}
.main-nav__wrapper {
  width: 260px;
  margin: 0 calc(50% - (260px / 2));
}
.main-nav__logo-wrapper {
  position: relative;
  flex-shrink: 0;
  padding: 22px 0;
}
.main-nav__logo-wrapper::before,
.main-nav__logo-wrapper::after {
  content: "";
  position: absolute;
  left: calc((100vw - 243px) / -2);
  width: 100vw;
  height: 1px;
  background-color: #ececec;
}
.main-nav__logo-wrapper::before {
  top: 0;
}
.main-nav__logo-wrapper::after {
  bottom: -1px;
}
.main-nav__link {
  display: block;
  margin-left: 50%;
  transform: translateX(-50%);
  width: 86px;
  height: 35px;
}
.main-nav__link:hover {
  fill: rgba(0, 0, 0, 0.6);
}
.main-nav__toggle {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  padding: 0;
  margin: 0;
  z-index: 2;
  display: block;
  width: 21px;
  height: 21px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.main-nav__toggle::before,
.main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 28px;
  height: 3px;
  background-color: #000000;
}
.main-nav__toggle::before {
  transform: translate(-50%, -50%) rotate(45deg);
}
.main-nav__toggle::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.main-nav__toggle--off {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  padding: 0;
  margin: 0;
  z-index: 2;
  display: block;
  width: 21px;
  height: 21px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.main-nav__toggle--off::before,
.main-nav__toggle--off::after {
  content: "";
  position: absolute;
  top: 4px;
  left: 0;
  width: 21px;
  height: 1px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
}
.main-nav--closed .main-nav__toggle {
  display: none;
}
.main-nav__text {
  display: none;
  margin: 0;
  padding: 0;
  padding-top: 18px;
  font-size: 15px;
  line-height: 24px;
  color: #444444;
  text-align: right;
  width: 300px;
  z-index: 1;
}
.site-list-product {
  display: none;
  z-index: 1;
}
.main-nav__logo:hover,
.main-nav__logo:focus {
  opacity: 0.6;
}
.main-nav__logo:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav {
    width: auto;
    margin: 0;
  }
  .main-nav__wrapper {
    width: 670px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    box-sizing: border-box;
  }
  .main-nav__logo-wrapper {
    width: 320px;
  }
  .main-nav__logo-wrapper::before,
  .main-nav__logo-wrapper::after {
    content: none;
  }
  .main-nav__link {
    margin: 0;
    height: 44px;
    width: 112px;
    transform: none;
  }
  .main-nav__logo {
    height: 46px;
    width: 112px;
  }
  .main-nav__toggle,
  .main-nav__toggle--off {
    display: none;
  }
  .main-nav__text {
    display: block;
    order: 3;
  }
  .main-nav__list {
    position: relative;
    order: 2;
    z-index: 1;
  }
  .main-nav__list::before {
    content: "";
    position: absolute;
    top: 0;
    left: calc((100vw - 653px) / -2);
    width: 100vw;
    height: 1px;
    background-color: #e7e7e7;
    z-index: 1;
  }
}
@media (min-width: 1150px) {
  .main-nav__wrapper {
    width: 1052px;
    box-sizing: border-box;
  }
  .main-nav__list::before {
    content: none;
  }
  .main-nav__logo-wrapper {
    padding: 9px 0 6px;
    order: 1;
    width: 296px;
  }
  .main-nav__link {
    height: 72px;
    width: 148px;
    margin-left: 48px;
  }
  .main-nav__logo {
    height: 72px;
    width: 148px;
  }
  .main-nav__list {
    order: 0;
  }
  .main-nav__list-user {
    order: 2;
  }
  .main-nav__list-product {
    position: relative;
    order: 3;
  }
  .main-nav__text {
    order: 4;
  }
  .main-nav__list-product::before {
    content: "";
    position: absolute;
    top: 0;
    left: calc((100vw - 1035px) / -2);
    width: 100vw;
    height: 1px;
    background-color: #e7e7e7;
    z-index: 1;
  }
}
.site-list {
  display: flex;
  flex-direction: column;
  font-weight: bold;
  margin: 0;
  padding: 0;
  list-style: none;
}
.site-list--closed {
