}
.site-list__link {
  display: block;
  padding: 27px 0 27px 46px;
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
  text-decoration: none;
  color: #000000;
}
.site-list__link::before {
  content: "";
  position: absolute;
  top: 0;
  left: calc((100vw - 243px) / -2);
  width: 100vw;
  height: 1px;
  background-color: #ececec;
  z-index: 1;
}
.site-list__link:hover,
.site-list__link:focus {
