}
@media (min-width: 768px) {
  .site-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .site-list a::before {
    content: none;
  }
  .site-list__item:last-child a::after {
    content: none;
  }
  .site-list__link {
    display: block;
    padding: 0;
    padding-top: 18px;
    font-size: 15px;
    width: 168px;
  }
}
@media (min-width: 1150px) {
